body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cursor-aura{position:absolute;background:radial-gradient(rgba(184, 183, 184, 0.7), rgba(38, 38, 125, 0));width:200px;height:200px;border-radius:50%;transition:opacity 250ms linear;z-index:0;-webkit-filter:blur(20px);filter:blur(20px)}
.bannerheader-container{position:absolute;z-index:2;top:20%;left:10%;font-family:"Raleway",sans-serif}.bannerheader-container>*{margin:0;pointer-events:none}.bannerheader-container>h1{color:#ecebea;font-size:3rem}.bannerheader-container>h2{color:rgba(220,217,211,.864);font-size:2rem}
.banner-container{width:100%;height:200px;background-color:#180a43;position:relative;overflow:hidden;isolation:isolate}.banner-tile{position:absolute;background-color:#180a43;width:44px;height:44px;margin:3px;z-index:1}.banner-highlight{width:100%;height:100%;position:absolute;top:60%;left:20%;border-radius:50% 0;-webkit-transform:rotateZ(-10deg);transform:rotateZ(-10deg);background:radial-gradient(rgba(184, 183, 184, 0.7), rgba(38, 38, 125, 0));-webkit-filter:blur(20px);filter:blur(20px);z-index:0}
.content-container{width:100%;display:flex;flex-direction:column;align-items:center}@media screen and (min-width: 1200px){.content-container{width:70%;margin:0 auto}}
#profileimage-container{width:240px;height:320px;display:none;background-color:#180a43;color:rgba(220,217,211,.864)}#profileimage-container img{width:320px;height:320px;transition:all 200ms linear}#profileimage-container img:hover{width:328px;height:328px}@media(min-width: 1200px){#profileimage-container{margin:50px;display:flex;justify-content:center;overflow:hidden}}
.paragraph-container{margin:0 30px;text-align:justify;color:#000;font-size:1.3rem;font-family:"Raleway",sans-serif;pointer-events:none}@media(min-width: 1200px){.paragraph-container{font-size:1.5rem}}
.projectlink-container{width:100%;height:300px;position:relative;margin-bottom:100px}.projectimage-container{width:400px;height:300px;overflow:hidden;display:flex;justify-content:center;align-items:center;margin:0 auto;border:5px solid #180a43;cursor:pointer}.projectimage-container:focus{box-shadow:0 0 40px #180a43;outline:none}.projectimage-container:active{box-shadow:none}.projectimage-container img{width:500px;height:300px;transition:all 200ms linear}.projectimage-container img:hover{width:630px;height:378px}.project-label{position:absolute;background-color:#480148;background:linear-gradient(100deg, #480148, #d275d2 10%, #480148);color:#ecebea;text-shadow:-1px -1px 1px #480148;font-size:2rem;font-weight:700;font-family:"Raleway",sans-serif;padding:.6em .9em;border:3px solid #ecebea;outline:3px solid #480148;bottom:-60px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);cursor:pointer}@media(min-width: 900px){.projectlink-container{width:49%}}@media(min-width: 1800px){.projectlink-container{width:33%}}
.contact-container{display:flex;flex-direction:column;margin-bottom:5em;font-size:1.5rem;font-family:"Raleway",sans-serif;font-weight:700;background-color:rgba(24,10,67,.6);background:linear-gradient(100deg, #180a43, #ecebea 10%, rgba(24, 10, 67, 0.6));height:400px;padding-top:3em}.resume-container{width:100%;position:relative}.contact-info-container{margin-top:3em;width:100%;height:100px;display:flex;flex-direction:column;justify-content:center;align-items:center}.contact-info-container p{color:#000}.resume-container a{margin:0 auto;padding-bottom:4px;width:90px;display:flex;flex-direction:column;align-items:center;justify-content:center;color:#000;text-decoration:none;height:100%}.resume-container a:focus{outline:none;border-bottom:4px solid #ecebea;padding-bottom:0}.resume-container a:hover{opacity:.7}.resume-icon{font-size:3rem}.resume-text{font-size:1.5rem;font-weight:700}p a:focus{text-decoration:none}@media(min-width: 1200px){.contact-container{flex-direction:row;height:250px;padding-top:0}.resume-container{width:45%}.resume-container a{height:85px;position:absolute;right:0;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.contact-info-container{width:60%;align-items:flex-start;padding-left:3em}}
.tech-container{border-top:4px solid #180a43;border-bottom:4px solid #180a43;display:flex;justify-content:space-evenly;flex-wrap:wrap;font-size:1.1rem;font-weight:700;margin:0 30px}.tech-container div{margin:1em 1.5em}.tech-display{font-size:1.3rem;font-family:"Raleway",sans-serif;font-weight:700}
.homepage-container{background:linear-gradient(#ecebea 60%, #180a43);width:100%}h1{font-family:"Raleway",sans-serif;font-size:3rem;color:#000;font-weight:700;text-align:center;pointer-events:none}#about-me-paragraph{padding:20px 0;background-color:#ecebea;position:relative;z-index:1;margin-top:50px}#about-me-paragraph::before{content:"";background:linear-gradient(to bottom right, #180a43, #ecebea);width:50%;height:50%;position:absolute;z-index:-2;top:-5px;left:10%}#about-me-paragraph::after{content:"";background:linear-gradient(to top left, #180a43, #ecebea);width:50%;height:50%;position:absolute;z-index:-2;bottom:-5px;right:10%}#paragraph-backing{position:absolute;background-color:#ecebea;top:0;width:100%;height:100%;z-index:-1}.section-wrapper{position:relative;height:100%;width:100%;display:flex;flex-direction:column;align-items:center;margin-bottom:100px}.skill-wrapper{margin-bottom:6em;margin-left:auto;margin-right:auto;width:95%}.skills-label{text-align:center;margin-bottom:1em;font-size:1.3rem;font-family:"Raleway",sans-serif;font-weight:700}@media(min-width: 1500px){.skill-wrapper{width:60%}#about-me-paragraph::before{left:-5px}#about-me-paragraph::after{right:-5px}}@media(min-width: 1200px){.projects-wrapper{flex-direction:row;flex-wrap:wrap}.section-wrapper{flex-direction:row}}
.nav-link,.nav-back{display:flex;align-items:center;cursor:pointer;color:#180a43;margin:0 20px;text-decoration:none;outline:none;border:none;background-color:transparent}.nav-link:hover,.nav-back:hover{opacity:.7}.nav-link:focus,.nav-back:focus{border-bottom:5px solid #180a43}.nav-link:active,.nav-back:active{border:none}.custom-icon{font-size:2rem;margin-right:10px}.link-text{font-family:"Raleway",sans-serif;font-size:2rem;font-weight:700;transition:all 200ms linear}
.projectnav-container{width:100%;height:100px;display:flex;justify-content:space-between;align-items:center}@media(min-width: 800px){.projectnav-container{width:70%}}
.sectionheader-container{display:flex;align-items:center;color:#ecebea;background-color:#480148;background:linear-gradient(100deg, #480148, #d275d2 70%, #480148);padding:.5em 1.5em;cursor:pointer;font-weight:600;font-family:"Raleway",sans-serif}.sectionheader-title{font-size:1.5rem}.section-icons{font-size:1.5rem;padding-right:10px}
.section-container{width:100%;height:100%;margin-bottom:10px;border:none;outline:none;transition:width 250ms linear}.section-container:focus{box-shadow:0 0 10px #000}.section-content-container{height:100%;overflow:hidden}.hide-content{height:0}.section-content{padding:30px}
